<template>
    <div>
      <!-- {{options}} -->
      <div id="charts-container-1" />
    </div>
</template>
<script type="text/ecmascript-6">
export default {
    props: {
        options: {
            type: Object,
            default() {
                return {
                  deviceNames: [], //设备名称
                  numbers: [],
                };
            }
        }
    },
    data() {
        return {};
    },

    watch: {
        options: {
            handler() {
                this.init();
            },
            deep: true
        }
    },
    mounted() {
        this.init();
    },

    methods: {
        /**
         * 初始化charts
         */
        init() {
          console.log(this.options)
             console.log('**************')
          const {
            deviceNames,
            numbers,
          } = this.options;
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(
                document.getElementById('charts-container-1')
            );


            // 指定图表的配置项和数据
            var option  ={
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'none'
                    },
                    formatter: function (params) {
                        return params[0].name + ': ' + params[0].value;
                    }
                },
                grid: {
                  left: '3%',
                  right: '9%',
                  bottom: '3%',
                  containLabel: true,
                  show: false                 // 网格边框是否显示，上和右边框
                },
                xAxis: {
                    name: '设备名',
                    data: deviceNames,
                    // axisTick: {show: false},
                    // axisLine: {show: false},
                    axisLabel: {
                        textStyle: {
                            color: '#666666'
                        }
                    }
                },
                yAxis: {
                    name: '数量',
                    splitLine: {show: false},
                    // axisTick: {show: false},
                    // axisLine: {show: false},
                    // axisLabel: {show: false}
                },
                color: ['#226FFF'],
                series: [{
                    name: 'hill',
                    type: 'pictorialBar',
                    barCategoryGap: '0%',
                    // symbol: 'path://M0,10 L10,10 L5,0 L0,10 z',
                    symbol: 'path://M0,10 L10,10 C5.5,10 5.5,5 5,0 C4.5,5 4.5,10 0,10 z',
                    // itemStyle: {
                    //     normal: {
                    //         opacity: 0.5
                    //     },
                    //     emphasis: {
                    //         opacity: 1
                    //     }
                    // },
                    data: numbers,
                    z: 10
                }]
            };

            myChart.setOption(option);

            /*窗口自适应*/
            window.addEventListener('resize', function() {
                myChart.resize();
            });
        }
    }
};
</script>
<style lang="less" scoped>
#charts-container-1 {
  height:300px;
}
</style>



