<template>
    <div>
      <!-- {{options}} -->
      <div id="charts-container" />
    </div>
</template>
<script type="text/ecmascript-6">
export default {
    props: {
        options: {
            type: Object,
            default() {
                return {
                  deviceNames: [], //设备名称
                  normalData: [], //正常
                  abnormalData: [], //异常
                };
            }
        }
    },
    data() {
        return {};
    },

    watch: {
        options: {
            handler() {
                this.init();
            },
            deep: true
        }
    },
    mounted() {
        this.init();
    },

    methods: {
        /**
         * 初始化charts
         */
        init() {
          console.log(this.options)
             console.log('**************')
          const {
            deviceNames,
            normalData,
            abnormalData,
          } = this.options;
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(
                document.getElementById('charts-container')
            );
            // 指定图表的配置项和数据
            var option  ={
              backgroundColor:'#FFFFFF',
              title: {
                textStyle:{
                  fontSize:14,
                }
              },
              tooltip: {
                trigger: 'axis',
                axisPointer: { // 坐标轴指示器，坐标轴触发有效
                  type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
                }
              },
              grid: {
                left: '3%',
                right: '9%',
                bottom: '3%',
                containLabel: true,
                show: false                 // 网格边框是否显示，上和右边框
              },
              xAxis: [{
                name: '设备名',
                type: 'category',
                splitLine: {                // 网格线 x轴对应的是否显示
                  show: false
                },
                data: deviceNames
              }],
              yAxis: [{
                name: '数量',
                type: 'value',
                splitLine: {                // 网格线 y轴对应的是否显示
                  show: false
                },
                axisLabel: {
                  formatter: '{value}'
                },
              }],
              series: [{
                name: '正常',
                type: 'bar',
                barWidth : 30,//柱图宽度
                itemStyle:{
                  normal:{color:'#226FFF'},
                },
                data: normalData
              },
              {
                name: '异常',
                type: 'bar',
                barWidth : 30,//柱图宽度
                itemStyle:{
                  normal:{color:'#FF8300'},
                },
                data:abnormalData
              }]
            };

            myChart.setOption(option);

            /*窗口自适应*/
            window.addEventListener('resize', function() {
                myChart.resize();
            });
        }
    }
};
</script>
<style lang="less" scoped>
#charts-container {
  height:300px;
}
</style>



