<template>
  <div class="echarts_box">
    <div class="echarts_query">
      <app-table-utils :utils="utils">
        <el-input disabled v-model="queryData.group" slot="query-1"/>
      </app-table-utils>
    </div>
    <div class="echgarts_switch">
      <div class="title">
        <span>开关状态统计</span>
        <p class="icon_echarts">
          <i class="icon_color"></i> <span class="icon_txt">正常</span>
          <i class="icon_color_err"></i> <span class="icon_txt">异常</span>
        </p>
      </div>
       <div class="echarts_height">
        <switch-echarts :options="switchData" />
      </div>
    </div>
    <div class="echgarts_number">
      <p class="title">设备数量统计</p>
      <div class="echarts_height">
        <number-echarts :options="numberData" />
      </div>
    </div>
  </div>
</template>
<script>
import SwitchEcharts from './dialog/SwitchEcharts';
import NumberEcharts from './dialog/NumberEcharts';
export default {
  name:'Group',
  components: {
    SwitchEcharts,
    NumberEcharts
  },
  data(){
    return {
        utils: { //表格工具栏
              left: [
                { label: '归属分组', slot: 'query-1' },
              ],
              cols: [24]
          },
        queryData:{
          group: '下拉框'
        },

      switchData:{
        deviceNames: ['v1','v2','v3','v4','v5','v6','v7','v8','v9','v10'], //设备名称
        normalData: [60,80,65,55,85,40,60,25,10,30], //正常
        abnormalData: [28,11,39,19,5,54,15,75,45,55], //异常
      },
      numberData:{
        deviceNames: ['v1','v2','v3','v4','v5','v6','v7','v8','v9','v10'], //设备名称
        numbers: [60,80,65,55,85,40,60,25,10,30],
      }

    }
  },
  created(){
    this.init();
  },
  methods:{
    init(){
      this.switchData = {
        deviceNames: ['v1','v2','v3','v4','v5','v6','v7','v8','v9','v10'], //设备名称
        normalData: [60,80,65,55,85,40,60,25,10,30], //正常
        abnormalData: [28,11,39,19,5,54,15,75,45,55], //异常
      },
       this.numberData = {
        deviceNames: ['v1','v2','v3','v4','v5','v6','v7','v8','v9','v10'], //设备名称
        numbers: [60,80,65,55,85,40,60,25,10,30],
      }
    }
  }
}
</script>
<style lang="less" scoped>
.echarts_box{
  box-sizing: border-box;
  margin: 20px 20px 0 20px;
  .echarts_query{
    background:rgba(255,255,255,1);
    box-shadow:0px 1px 4px 0px rgba(147,150,152,0.1);
    border-radius:2px;
    box-sizing: border-box;
    /deep/ .app-table-utils{
      height: 70px;
      box-sizing: border-box;
      /deep/ .flex-container{
        margin: 20px 0 20px 20px;
      }
    }
  }
  .echgarts_switch,.echgarts_number{
    margin: 20px 0 0 0;
    height:360px;
    background:rgba(255,255,255,1);
    box-shadow:0px 1px 4px 0px rgba(147,150,152,0.1);
    border-radius:2px;
  }

  .title{
    height: 60px;
    line-height: 60px;
    font-size:18px;
    font-family: 'PingFang-SC-Medium';
    font-weight:bold;
    color:rgba(51,51,51,1);
    padding: 0 0 0 20px;
    clear:both
  }
  .echarts_height{
    height: 300px;
    box-sizing: border-box;
  }

  .icon_echarts{
    float: right;
    width: 180px;
    height: 60px;
    line-height: 60px;
    box-sizing: border-box;
    .icon_color{
      display: inline-block;
      width:20px;
      height:6px;
      background:rgba(34,111,255,1);
      border-radius:2px;
      margin: 0 0 2px 0;
    }
    .icon_txt{
      display: inline-block;
      font-size:14px;
      font-family: 'PingFang-SC-Medium';
      font-weight:500;
      color:rgba(51,51,51,1);
    }
    .icon_color_err{
      display: inline-block;
      width:20px;
      height:6px;
      background:#FF8300;
      border-radius:2px;
      margin: 0 0 2px 20px;
    }
  }
}
</style>




